body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-transform: capitalize;
}
.pic {
  font-size: 18px;
  font-weight: 400;
  margin-right: 20px;
}

.card-img3 {
  width: 100%;
  height: 300px;
}
.foodRequestStatusImage {
  height: 100px;
  width: 100%;
  text-align: center;
}

.bg-img {
  min-height: 100vh;
  background: url("./img/logo.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.donar {
  height: 100vh;
  background: url("./img/donate.jpg") 100% no-repeat;
}

.login {
  min-height: 100vh;
  background: url("./img/login.png") 20% 20% no-repeat;
  background-size: cover;
}

.dashboard {
  min-height: 100vh;
  padding: 50px;
  background: url("./img/tile.png.crdownload");
}

.card-img {
  height: 150px;
  width: 150px;
  margin: auto;
  text-align: center;
}

.card-img2 {
  height: 100px;
  width: 100px;
  margin: auto;
  text-align: center;
}

.backbtn {
  position: fixed !important;
  z-index: 99999;
  top: 10px;
  left: 10px;
}

table {
  text-align: left;
  position: relative !important;
  border-collapse: collapse;
}

table thead tr th {
  background: white;
  position: sticky !important;
  top: 65px; /* Don't forget this, required for the stickiness */
}

table caption {
  font-size: 2em;
  margin: 0.5em 0 0.75em;
}

table tr {
  border: 1px solid #9a9a9a80;
  padding: 0.35em;
  line-height: 3vh;
}

table th,
table td {
  text-transform: capitalize;
}

table th {
  font-size: 0.85em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

@media only screen and (max-width: 600px) {
  table {
    border: 0;
    width: 80vw;
    box-shadow: none;
  }
  table caption {
    font-size: 2em;
  }
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  table tr {
    border-bottom: 3px solid #00000060;
    border-right: 3px solid #00000040;
    background: linear-gradient(100deg, #aaaaaa00, #fff);
    border-radius: 0 0 4vw 0;
    display: block;
    margin-bottom: 0.625em;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }
  table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  table td:last-child {
    border-bottom: 0;
  }
}
